.gallary {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1572px;
  margin: 0 auto;
}

.gallaryItem {
  width: 500px;
  height: 500px;
  margin: 12px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.gallaryItem:last-child {
  margin-right: auto;
}

@media (max-width: 1571px) {
  .gallaryItem:last-child {
    margin-right: 12px;
  }
}

.gallaryItemWrapper {
  position: relative;
  cursor: pointer;
}

.gallaryItemWrapperVideo {
  position: relative;
  cursor: pointer;
}

.gallaryItemWrapper::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #000000;
  z-index: 1;
  opacity: 0;
  transition: all .2s;
}

.gallaryItemWrapper::after {
  content: url('../assets/img/fullscreen.svg');
  position: absolute;
  top: 50%;
  left: 50%;
  width: 15%;
  height: auto;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: all .2s;
  z-index: 1;
}

@media (max-width: 786px) {
  .gallaryItem {
    height: auto;
  }

  .gallaryItemWrapper::after {
    display: none;
  }
 
}

.gallaryItemWrapper:hover::before {
  opacity: .6;
}
.gallaryItemWrapper:hover::after {
  opacity: 1;
}


.gallaryItem img {
  width: 100%;
  height: auto;
  display: block;
  position: relative;
  transition: all 0.4s ease;
}

.gallaryItem video {
  width: 100%;
  height: auto;
  display: block;
  position: relative;
}


.modal {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #242424;
  transition: opacity 0.2s ease, visibility 0.2s ease,
    transform 0.3s ease-in-out;
  visibility: hidden;
  opacity: 0;
  transform: scale(0);
  overflow: hidden;
  z-index: 999;
}

.open {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}

.modal img {
  width: auto;
  max-width: 100%;
  height: auto;
  max-height: 100%;
  display: block;
  line-height: 0;
  box-sizing: border-box;
  padding: 20px 0 20px;
  margin: 0 auto;
}

.close {
  position: fixed;
  top: 10px;
  right: 10px;
  padding: 5px;
  background-color: black;
  cursor: pointer;
  width: 4rem;
  height: 4rem;
  display: flex;
  border-radius: 50%;
}